@use "../settings/init" as *;

.custom-hero-banner-section {
  background-color: var(--section-bg-color);
  padding: rem(40) 0 0 0;

  @media (max-width: 992px) {
    padding: 0;

    xo-container {
      padding: 0;
    }
  }
}

.custom-hero-banner {
  position: relative;
  min-height: rem(700);
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: var(--bg-desktop);
  overflow: hidden;

  @media (max-width: 992px) {
    min-height: rem(557);
    justify-content: center;
    background-image: var(--bg-mobile) !important;
  }

  &__content-box {
    position: relative;
    z-index: 2;
    background: var(--color-midnight-blue);
    color: var(--color-blue-40);
    padding: rem(54) rem(120) rem(80) rem(120);
    margin-left: rem(48);

    @media (max-width: 992px) {
      background: none;
      margin: rem(32) rem(8);
      padding: rem(32) rem(16);
      max-width: 100%;
      text-align: center;
      color: var(--color-white);
    }
  }

  &__heading {
    font-size: rem(60);
    line-height: 1 !important;
    margin-top: 0;
    font-weight: 300;
    font-family: var(--font-beausite-fit-web);
    color: var(--color-blue-80);
    margin-bottom: rem(24);
    max-width: rem(460);

    @media (max-width: 992px) {
      font-size: rem(42) !important;
      text-align: center;
      max-width: 100%;
      margin-bottom: rem(20) !important;
    }
  }

  &__subheading,
  &__desc {
    font-size: rem(18);
    line-height: 1.6;
    color: var(--color-white);

    @media (max-width: 992px) {
      font-size: rem(14) !important;
    }

    strong {
      font-weight: 600;
    }
  }

  &__desc {
    margin-bottom: rem(72);

    @media (max-width: 992px) {
      margin-bottom: rem(24);
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: nowrap;
    gap: rem(24);
    margin-top: rem(24);

    @media (max-width: 992px) {
      flex-wrap: wrap;
      flex-direction: column;
      gap: rem(16);
      align-items: center;
    }
  }

  &__button {
    display: inline-block;
    padding: rem(12) rem(69);
    border-radius: rem(120);
    font-size: rem(18);
    line-height: 1.6;
    font-weight: 300;
    text-align: center;
    text-decoration: none;
    transition: var(--default-transition);

    @media (max-width: 992px) {
      font-size: rem(14) !important;
      padding: rem(16) rem(24);
    }

    &--primary {
      background: var(--color-blue-80);
      color: var(--color-black);

      @media (max-width: 992px) {
        width: 100%;
      }

      &:hover {
        background: var(--color-blue-120);
      }
    }

    &--secondary {
      background: transparent;
      color: var(--color-neutral-cream);
      opacity: 1;
      border: none;
      display: flex;
      align-items: center;
      gap: rem(24);

      &:hover {
        opacity: 0.8;
      }

      @media (max-width: 992px) {
        justify-content: center;
      }
    }
  }
}
